import graceMaskView from "./MyGraceMaskView";
export default {
    props: {
        show: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            show2: false
        }
    },
    watch:{
        show(){
            this.show2=this.show;
        }
    },
    methods: {
        // 第1个演示 开启与关闭
        showBanner: function() {
            this.show = true;
        },
        closeBanner: function() {
            this.show = false;
        },
        // 第2个演示 开启与关闭
        showBanner2: function() {
            this.show2 = true;
        },
        closeBanner2: function() {
            this.show2 = false;
            this.$emit('close');
        }
    },
    components: {
        graceMaskView
    }
}
